import React from 'react';
import axios from 'axios';
import {Badge, Card, Col, Container, ProgressBar, Row} from "react-bootstrap";
import Header from "../../header/Header";
import './pokemonStyle.css'

const TYPE_COLORS = {
    bug: 'B1C12E',
    dark: '4F3A2D',
    dragon: '755EDF',
    electric: 'FCBC17',
    fairy: 'F4B1F4',
    fighting: '823650',
    fire: 'E73B0C',
    flying: 'A3B3F7',
    ghost: '6060B2',
    grass: '74C236',
    ground: 'D3B357',
    ice: 'A3E7FD',
    normal: 'C8C4BC',
    poison: '934594',
    psychic: 'ED4882',
    rock: 'B9A156',
    steel: 'B5B5C3',
    water: '3295F6'
};

class Pokemon extends React.Component{
    state = {
        name: '',
        pokemonIndex: '',
        imageUrl: '',
        types: [],
        description: '',
        stats: {
            hp: '',
            attack: '',
            defense: '',
            speed: '',
            specialAttack: '',
            specialDefense: '',
        },
        height: '',
        weight: '',
        eggGroup: '',
        abilities: '',
        genderRatioMale: '',
        genderRatioFemale: '',
        evs: '',
        hatchSteps: '',
    };

    async componentDidMount() {
        const { pokemonIndex } = this.props.match.params;
        const pokemonUrl = `https://pokeapi.co/api/v2/pokemon/${pokemonIndex}/`;
        const pokemonSpeciesUrl = `https://pokeapi.co/api/v2/pokemon-species/${pokemonIndex}/`;

        const pokemonRes = await axios.get(pokemonUrl);
        const name = pokemonRes.data.name;

        const imageUrl = pokemonRes.data.sprites.front_default;

        let {hp, attack, defense, speed, specialAttack, specialDefense} = '';

        pokemonRes.data.stats.map(stat => {
            switch (stat.stat.name) {
                case 'hp':
                    hp = stat['base_stat'];
                    break;
                case 'attack':
                    attack = stat['base_stat'];
                    break;
                case 'defense':
                    defense = stat['base_stat'];
                    break;
                case 'speed':
                    speed = stat['base_stat'];
                    break;
                case 'special-attack':
                    specialAttack = stat['base_stat'];
                    break;
                case 'special-defense':
                    specialDefense = stat['base_stat'];
                    break;
            }
        });

        const height =
            Math.round((pokemonRes.data.height * 0.328084 + 0.0001) * 100) / 100;
        const weight =
            Math.round((pokemonRes.data.weight * 0.220462 + 0.0001) * 100) / 100;

        const types = pokemonRes.data.types.map(type => type.type.name);

        const abilities = pokemonRes.data.abilities.map(ability => {
            return ability.ability.name.toLowerCase()
        }).join(', ');

        const evs = pokemonRes.data.stats.filter(stat => stat.effort > 0)
            .map(stat =>`${stat.effort} ${stat.stat.name}`.toLowerCase()).join(', ');

        await axios.get(pokemonSpeciesUrl).then(res => {
            const description =  res.data.flavor_text_entries.find(flavor => flavor.language.name === 'en').flavor_text;
            const femaleRate = res.data[`gender_rate`];
            const genderRatioFemale = 12.5 * femaleRate;
            const genderRatioMale = 12.5 * (8 - femaleRate);
            const catchRate = Math.round((100 / 255) * res.data[`capture_rate`]);
            const eggGroups = res.data[`egg_groups`].map(group => group.name.toLowerCase()).join(', ');
            const hatchSteps = 255 * (res.data[`hatch_counter`] * 1);

            this.setState({
                description,
                genderRatioFemale,
                genderRatioMale,
                catchRate,
                eggGroups,
                hatchSteps,
            })
        });

        this.setState({
            name,
            imageUrl,
            pokemonIndex,
            types,
            stats: {
                hp,
                attack,
                defense,
                speed,
                specialAttack,
                specialDefense,
            },
            height,
            weight,
            abilities,
            evs,
        })
    }

    render() {
        return (
            <div>
                <Header/>
                <Container className="col-9 mt-5">
                    <Card>
                        <Card.Header>
                            <h5>
                                {this.state.pokemonIndex}
                                {this.state.types.map(type => (
                                    <Badge
                                        key={type}
                                        className="badge-pill float-right m-1"
                                        style={{backgroundColor: `#${TYPE_COLORS[type]}`}}
                                    >
                                        {type}
                                    </Badge>
                                ))}
                            </h5>
                        </Card.Header>
                        <Card.Body>
                            <Card.Title className="text-center">
                                {this.state.name.toUpperCase()}
                            </Card.Title>
                            <Card.Text>
                                <Row className="align-items-center">
                                    <img
                                        className="rounded mx-auto mt-2 col-md-3"
                                        src={this.state.imageUrl}
                                    />
                                    <Col>
                                        <Row>
                                            <div className="col-12 col-md-3">
                                                <span className="mySpan">HP:</span>
                                            </div>
                                            <div className="col-12 col-md-9">
                                                <ProgressBar
                                                    variant="success"
                                                    now={this.state.stats.hp}
                                                    label="HP"/>
                                            </div>
                                        </Row>
                                        <Row>
                                            <div className="col-12 col-md-3">
                                                <span className="mySpan">Attack:</span>
                                            </div>
                                            <div className="col-12 col-md-9">
                                                <ProgressBar
                                                    variant="danger"
                                                    now={this.state.stats.attack}
                                                    label="ATT"/>
                                            </div>
                                        </Row>
                                        <Row>
                                            <div className="col-12 col-md-3">
                                                <span className="mySpan">Defense:</span>
                                            </div>
                                            <div className="col-12 col-md-9">
                                                <ProgressBar
                                                    variant="info"
                                                    now={this.state.stats.defense}
                                                    label="DEF"/>
                                            </div>
                                        </Row>
                                        <Row>
                                            <div className="col-12 col-md-3">
                                                <span className="mySpan">Speed:</span>
                                            </div>
                                            <div className="col-12 col-md-9">
                                                <ProgressBar
                                                    variant="warning"
                                                    now={this.state.stats.speed}
                                                    label="SPEED"/>
                                            </div>
                                        </Row>
                                        <Row>
                                            <div className="col-12 col-md-3">
                                                <span className="mySpan">Special Attack:</span>
                                            </div>
                                            <div className="col-12 col-md-9">
                                                <ProgressBar
                                                    variant="danger"
                                                    now={this.state.stats.specialAttack}
                                                    label="S-ATT"/>
                                            </div>
                                        </Row>
                                        <Row>
                                            <div className="col-12 col-md-3">
                                                <span className="mySpan">Special Defense:</span>
                                            </div>
                                            <div className="col-12 col-md-9">
                                                <ProgressBar
                                                    variant="info"
                                                    now={this.state.stats.specialDefense}
                                                    label="S-DEF"/>
                                            </div>
                                        </Row>
                                    </Col>
                                </Row>
                                <Row className="m-2">
                                    <span className="mySpan">{this.state.description}</span>
                                </Row>
                                <Col className="m-2 mt-5 mb-4 text-center">
                                    <h5>Profile</h5>
                                </Col>
                                <Row className="col-md-12">
                                    <Col className="col-md-6">
                                        <Row>
                                            <div className="col-6">
                                                <span className="float-right mySpan mySpan">Height:</span>
                                            </div>
                                            <div className="col-6">
                                                <span className="float-left mySpan">{this.state.height} ft.</span>
                                            </div>
                                            <div className="col-6">
                                                <span className="float-right mySpan">Weight:</span>
                                            </div>
                                            <div className="col-6">
                                                <span className="float-left mySpan">{this.state.weight} ft.</span>
                                            </div>
                                            <div className="col-6">
                                                <span className="float-right mySpan">Catch Rate:</span>
                                            </div>
                                            <div className="col-6">
                                                <span className="float-left mySpan">{this.state.catchRate}%</span>
                                            </div>
                                            <div className="col-6">
                                                <span className="float-right mySpan">Gender Ratio:</span>
                                            </div>
                                            <div className="col-6">
                                                <ProgressBar>
                                                    <ProgressBar
                                                        variant="info"
                                                        now={this.state.genderRatioMale}
                                                        label="MALE"/>
                                                    <ProgressBar
                                                        variant="danger"
                                                        now={this.state.genderRatioFemale}
                                                        label="FEMALE"/>
                                                </ProgressBar>
                                            </div>
                                        </Row>
                                    </Col>
                                    <Col className="col-md-6">
                                        <Row>
                                            <div className="col-6">
                                                <span className="float-right mySpan mySpan">Egg Groups:</span>
                                            </div>
                                            <div className="col-6">
                                                <span className="float-left mySpan">{this.state.eggGroups}</span>
                                            </div>
                                            <div className="col-6">
                                                <span className="float-right mySpan">Hatch Steps:</span>
                                            </div>
                                            <div className="col-6">
                                                <span className="float-left mySpan">{this.state.hatchSteps}</span>
                                            </div>
                                            <div className="col-6">
                                                <span className="float-right mySpan">Abilities:</span>
                                            </div>
                                            <div className="col-6">
                                                <span className="float-left mySpan">{this.state.abilities}</span>
                                            </div>
                                            <div className="col-6">
                                                <span className="float-right mySpan">EVS:</span>
                                            </div>
                                            <div className="col-6">
                                                <span className="float-left mySpan">{this.state.evs}</span>
                                            </div>
                                        </Row>
                                    </Col>
                                </Row>
                            </Card.Text>
                        </Card.Body>
                    </Card>
                </Container>
            </div>
        )
    }
}

export default Pokemon



