import React from 'react';
import {Dropdown} from "react-bootstrap";
import {inject, observer} from "mobx-react/index";

@inject('pokemonStore')
@observer
class DropdownPokedex extends React.Component{
    render() {
        return (
            <Dropdown className="m-4 p-0">
                <Dropdown.Toggle variant="outline-warning">
                    Amount Pokemon
                </Dropdown.Toggle>
                <Dropdown.Menu>
                    <Dropdown.Item
                        onClick={() => {this.props.pokemonStore.amountPokemon(this.props.pokemonStore.limit = 10)}}
                    >
                        Amount: 10
                    </Dropdown.Item>
                    <Dropdown.Item
                        onClick={() => {this.props.pokemonStore.amountPokemon(this.props.pokemonStore.limit = 20)}}
                    >
                        Amount: 20
                    </Dropdown.Item>
                    <Dropdown.Item
                        onClick={() => {this.props.pokemonStore.amountPokemon(this.props.pokemonStore.limit = 50)}}
                    >
                        Amount: 50
                    </Dropdown.Item>
                </Dropdown.Menu>
            </Dropdown>
        )
    }
}

export default DropdownPokedex



